@use "base/variables" as v;
@use "base/mixins" as m;

.crear {

    .uptask {
        @include m.textoDegradado(v.$morado);
    }
    input[type="submit"] {
        @include m.boton (v.$morado, v.$blanco);
    }
    .acciones {
        a {
            color: v.$morado;
        }
    }
}